ara influir en el comportamiento de otros, persuadiendo a Eva y, a través de ella, a Adán. Así, se puede interpretar que, desde los tiempos más antiguos, han existido formas de comunicación persuasiva, orientadas a influir en las decisiones de otros, lo que da origen a lo que hoy se conoce como mercadeo o "),_c('i',[_vm._v("marketing")]),_vm._v(".")])])]),_c('div',{staticClass:"col-4 d-none d-xl-block",attrs:{"data-aos":"zoom-in"}},[_c('img',{attrs:{"src":require("@/assets/curso/unidad/img-20.png"),"alt":"Texto que describa la imagen"}})])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',{staticClass:"mb-4"},[_c('b',[_vm._v("Figura 1.")]),_vm._v(" Evolución del mercadeo")])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_2_1","data-aos":"flip-up"}},[_c('div',{staticClass:"d-flex"},[_c('span',{staticClass:"me-3"},[_vm._v("2.1")]),_c('h2',[_vm._v("Orientación hacia el producto ")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-4 d-none d-xl-block",attrs:{"data-aos":"zoom-in"}},[_c('img',{attrs:{"src":require("@/assets/curso/unidad/img-22.png"),"alt":"Texto que describa la imagen"}})]),_c('div',{staticClass:"col-12 col-xl-8"},[_c('p',{staticClass:"mb-4"},[_vm._v("Después de dejar atrás la época del trueque, donde los bienes se intercambiaban directamente sin usar dinero, el concepto de mercadeo comenzó a desarrollarse a comienzos del siglo XX. En ese entonces, las organizaciones centraban su atención en la producción, y el conocimiento sobre mercadeo, era aún limitado. Las decisiones sobre precios dependían más de las áreas de producción y finanzas, que de un análisis del mercado. El papel del área de ventas consistía simplemente en colocar el producto en el mercado, sin considerar otras variables. La lógica empresarial se basaba en fabricar y distribuir el mejor producto posible, bajo la idea de que todo lo que se producía se vendía sin dificultad, puesto que la demanda superaba a la oferta disponible.")]),_c('div',{staticClass:"d-flex align-items-start bg-amarillo p-4 d-flex align-items-center flex-column flex-md-row mb-4",attrs:{"data-aos":"fade-up"}},[_c('img',{staticClass:"me-3",staticStyle:{"width":"90px"},attrs:{"src":require("@/assets/curso/unidad/img-23.svg"),"alt":"Texto descriptivo de la imagen"}}),_c('div',{staticClass:"my-auto p-4 p-xl-0"},[_c('p',{staticClass:"pe-xl-3 mb-0"},[_vm._v("Hoy en día, aunque muchas cosas han cambiado, todavía existen empresas que mantienen este enfoque. Para ellas, el mercado está compuesto por consumidores similares, cuyas necesidades pueden ser satisfechas con la función esencial del producto, ofrecido a un precio razonable. Estas organizaciones dirigen sus esfuerzos hacia la eficiencia en la fabricación y distribución, creyendo que si el producto es técnicamente bueno y está bien distribuido, los consumidores lo aceptarán sin mayores exigencias.")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_2_3","data-aos":"flip-up"}},[_c('div',{staticClass:"d-flex"},[_c('span',{staticClass:"me-3"},[_vm._v("2.3")]),_c('h2',[_vm._v("Orientación hacia las ventas")])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-12 col-xl-9"},[_c('p',{staticClass:"mb-4"},[_vm._v("La orientación hacia las ventas aparece en la década de 1930, en un contexto marcado por la Gran Depresión. Durante este periodo, las empresas comienzan a enfrentar una realidad diferente: la cantidad de productos disponibles supera el interés de los consumidores. En este nuevo escenario, ya no basta con fabricar artículos de calidad, el verdadero reto está en encontrar la manera de venderlos. Esto lleva a que las organizaciones asuman nuevas tareas enfocadas en mejorar la distribución, aumentar la promoción y fortalecer su equipo de ventas.")]),_c('div',{staticClass:"bg-naranja p-4",attrs:{"data-aos":"fade-right"}},[_c('span',[_vm._v("En este enfoque, los gerentes comienzan a ver el mercadeo como un proceso centrado en la venta directa y persuasiva. Se hace uso de estrategias intensas para convencer al consumidor de realizar una primera compra, sin prestar demasiada atención a si volverá o no. Es decir, el objetivo es cerrar la venta, no necesariamente construir una relación duradera con el cliente.")])])]),_c('div',{staticClass:"col-3 d-none d-xl-block",attrs:{"data-aos":"zoom-in"}},[_c('img',{attrs:{"src":require("@/assets/curso/unidad/img-24.png"),"alt":"Texto que describa la imagen"}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-4"},[_c('div',{staticClass:"col-5 d-none d-xl-block",attrs:{"data-aos":"zoom-in"}},[_c('img',{attrs:{"src":require("@/assets/curso/unidad/img-25.png"),"alt":"Texto que describa la imagen"}})]),_c('div',{staticClass:"col-12 col-xl-7"},[_c('div',{staticClass:"bg-azul p-4",attrs:{"data-aos":"fade-left"}},[_c('span',[_vm._v("Este tipo de orientación es común en empresas que ofrecen productos o servicios que la gente no suele buscar de manera activa, como los seguros exequiales o la venta de terrenos en cementerios. Desde una visión administrativa, esta manera de entender el mercadeo está más enfocada a liberar espacio en las bodegas, que en satisfacer al cliente, porque lo importante es reducir los inventarios y mover los productos fuera de la empresa.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"titulo-segundo",attrs:{"id":"t_2_3","data-aos":"flip-up"}},[_c('div',{staticClass:"d-flex"},[_c('span',{staticClass:"me-3"},[_vm._v("2.3")]),_c('h2',[_vm._v("Orientación hacia el consumidor")])])])},funct